import makeStyles from "@mui/styles/makeStyles";
import Container from "@mui/material/Container";
import Stack from "@mui/material/Stack";
import Typography from "@mui/material/Typography";
import Grid from "@mui/material/Grid";
import React from "react";
import { useNavigate } from "react-router-dom";
import { Swiper, SwiperSlide } from "swiper/react";
import { Banner, BottomNav, TopNav } from "../../components";
const useStyles = makeStyles(() => ({
  textRoll: {
    position: "relative",
    paddingBottom: "16vh",
    paddingTop: "16px",
  },
}));

const NewsPage = () => {
  const navigate = useNavigate();
  const classes = useStyles();
  return (
    <div className={classes.textRoll}>
      <Stack spacing={2}>
        <TopNav type="back" title={"Berita"} click={() => navigate(-1)} />
        <Swiper
          // pagination={true}
          slidesPerView={1.1}
          spaceBetween={10}
          className="mySwiper"
        >
          {[1, 2, 3, 4].map((item, index) => {
            return (
              <SwiperSlide key={"carousel_" + index}>
                <Banner variant="news" />
              </SwiperSlide>
            );
          })}
        </Swiper>
        <Container>
          <Grid container spacing={2}>
            <Grid item xs={12}>
              <Stack spacing={0}>
                <Typography variant="subtitle2">Harga Baru PCR</Typography>
                <Typography variant="body1">
                  Penulis: RSUD Cengkareng
                </Typography>
                <Typography variant="body1">05-11-2021</Typography>
              </Stack>
            </Grid>
            <Grid item xs={12}>
              <Typography variant="body1" align="justify">
                Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do
                eiusmod tempor incididunt ut labore et dolore magna aliqua. At
                consectetur lorem donec massa sapien faucibus et. Gravida in
                fermentum et sollicitudin ac orci. Sit amet porttitor eget
                dolor. Lorem dolor sed viverra ipsum nunc aliquet bibendum enim.
                Vitae tempus quam pellentesque nec. Proin sed libero enim sed
                faucibus turpis in eu. Sed elementum tempus egestas sed. Orci
                nulla pellentesque dignissim enim sit amet venenatis. Eget duis
                at tellus at urna condimentum. Cras ornare arcu dui vivamus arcu
                felis bibendum. Sem integer vitae justo eget magna fermentum
                iaculis eu non. Elit ut aliquam purus sit amet luctus. Diam quam
                nulla porttitor massa id. Consequat ac felis donec et. Sit amet
                massa vitae tortor condimentum lacinia quis vel. Magna ac
                placerat vestibulum lectus mauris ultrices eros in cursus.
                Mattis nunc sed blandit libero.
              </Typography>
            </Grid>
          </Grid>
        </Container>
      </Stack>
      <BottomNav />
    </div>
  );
};

export default NewsPage;
